<template>
  <span>{{ priceString }}</span>
</template>

<script>
export default {
  props: ['startsAt', 'lineItem'],
  data() {
    if (window.shippingOption === undefined) window.shippingOption = { option: undefined }
    return {
      shippingOption: window.shippingOption,
    }
  },
  computed: {
    priceString() {
      let price = 0
      if (this.lineItem === true || this.lineItem === 'true') {
        price = (this.shippingOption.option === 'delivery' ? 0 : 500)
      } else {
        price = (this.shippingOption.option === 'delivery' ? 0 : 500) + parseInt(this.startsAt, 10)
      }

      if (price === 0) {
        return '$0.00'
      }

      return `$${price.toString().slice(0, price.toString().length - 2)}.${price.toString().slice(-2)}`
    },
  },
  name: 'DynamicPrice',
}
</script>

<style scoped lang="sass">@import "../style/core"</style>
