<template>
  <div class="radiocontainer">
    <span
        v-for="option in radioOptionsParsed"
        :key="option.id"
        v-bind:class="shippingOption.option === option.id ? 'radio radio-selected' : 'radio'"
        @click="shippingOption.option = option.id"
    >
      {{ option.text }}
    </span>
  </div>
</template>

<script>
export default {
  props: ['radioOptions'],
  data() {
    const parsed = JSON.parse(this.radioOptions).items
    if (window.shippingOption === undefined) window.shippingOption = { option: undefined }

    return {
      radioOptionsParsed: parsed,
      shippingOption: window.shippingOption,
    }
  },
  name: 'RadioMenu',
}
</script>

<style scoped lang="sass">@import "../style/core"</style>
