<script>
import HorizontalLine from '../../components/HorizontalLine.vue'
import RadioMenu from '../../components/RadioMenu.vue'
import DynamicPrice from '../../components/DynamicPrice.vue'

export default {
  components: {
    DynamicPrice,
    RadioMenu,
    HorizontalLine,
  },
  name: 'StrawberryExecutive',
  methods: {
    openStripe: () => {
      window.plausible(
        'Checkout',
        {
          props: {
            shippingType: window.shippingOption.option,
          },
        },
      )

      setTimeout(() => {
        if (window.shippingOption.option === 'delivery') {
          window.location.href = 'https://buy.stripe.com/fZe5lA8J02rj5XyeUV'
        } else {
          window.location.href = 'https://buy.stripe.com/6oEg0egbs2rj99K288'
        }
      }, 500)
    },
  },
  data() {
    if (window.shippingOption === undefined) window.shippingOption = { option: undefined }
    return {
      shippingOption: window.shippingOption,
      customized: false,
    }
  },
}
</script>

